var baseChart = {
    minHeight: 240,
    aspectRatio: 3.8,
    margins: {top: 10, left: 45, right: 30, bottom: 120},
    color: "#dfaf00"
}

var majcomChart = {
    minHeight: 200,
    aspectRatio: 4.2,
    margins: {top: 10, left: 45, right: 30, bottom: 80},
    color: "#1976d2"
}

module.exports = {
    baseChart,
    majcomChart
}



